.content {
    padding: 10px;
    min-height: calc(100vh - 50px);
}

.section {
    border-left: 1px solid #2a444a;
    max-width: 1300px;
    margin: 0 auto;
    padding-left: 20px;
}

.section__title {
    color: #2a444a;
    font-size: 40px;
    margin: 20px 0;
    text-transform: capitalize;
}

.section__p {
    color: #2a444a;
}


