.home {
  color: #2a444a;
  padding: 30px;
  font-size: 50px; }

.home__title {
  -webkit-animation: sliding 1s cubic-bezier(0.39, 0.575, 0.565, 1);
          animation: sliding 1s cubic-bezier(0.39, 0.575, 0.565, 1); }

.home__subtitle {
  -webkit-animation: sliding 1.7s cubic-bezier(0.39, 0.575, 0.565, 1);
          animation: sliding 1.7s cubic-bezier(0.39, 0.575, 0.565, 1); }

@-webkit-keyframes sliding {
  from {
    -webkit-transform: translatey(20px);
            transform: translatey(20px);
    opacity: 0; } }

@keyframes sliding {
  from {
    -webkit-transform: translatey(20px);
            transform: translatey(20px);
    opacity: 0; } }



